import {Injectable} from '@angular/core';
import {Http} from '@angular/http';

@Injectable()
export class DictionaryService {

    constructor(private http:Http) {
    }

    getDataFromServer() {
        return this.http.get('http://worldcup.sfg.io/teams/results')
    }
}



